.formClassContainer{
    margin: 40px auto;
    width: 405px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.inputText{
    width: 100%;
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    & > label{
        font-weight: bold;
        font-size: 14px;
        margin-right: 5px;
    }
    
    & > input {
        width: 300px;
        border: none;
        border-bottom: 1px solid black;
        font-size: 14px;
        padding-left: 10px;
    }
    
    & > input::placeholder{
        color: #bbb;
    }
    
    & >input:focus-visible{
        outline: none;
        border-bottom: 2px solid black;
    }
}



.buttonClass{
    width: 100px;
    font-size: 24px;
    color: white;
    background-color: var(--primary);
    border-radius: 10px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    border: none;
    cursor: pointer;
}

