.search-form {
  & > button {
    width: 100px;
    height: 50px;
    color: white;
    cursor: pointer;
    background-color: transparent;
    border-radius: 10px;
    font-size: 24px;
    text-align: center;
    border: 2px solid white;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  & > input {
    width: 500px;
    height: 50px;
    font-size: 24px;
    border: 2px solid white;
    border-radius: 10px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    margin-right: 15px;
    background-color: transparent;
    padding-left: 20px;
  }
  & > input::placeholder {
    color: #bbb;
  }
}
